  font-size: 16px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 10px;
}

.back-link {
  margin-bottom: 15px;
}

.section {
  text-align: center;
  margin-bottom: 8px;

  &__message {
    margin-bottom: 8px;
  }

  &__loader {
    margin-top: 16px;
  }

  &__button {
    margin-bottom: 16px;
  }
}

.jit-section {
  margin-top: 16px;
  padding: 12px;
  background-color: var(--secondary-bg);
  border-radius: 8px;

  &__notice {
    margin-bottom: 12px;
    line-height: 1.3;
  }

  &__success {
    color: var(--text-success);
    font-weight: 500;
    margin-bottom: 12px;
  }

  &__button {
    margin-top: 8px;
  }
}

.commands {
  margin-top: 16px;
}

/* Connection Dialog Styles */
.connection-dialog {
  padding: 15px;
  max-height: 100%;

  &__content {
    margin-bottom: 15px;
    white-space: pre-line;

    p {
      margin: 0;
      line-height: 1.4;
    }
  }

  &__buttons {
    display: flex;
    justify-content: flex-end;
    border-top: 1px solid var(--tertiary-accent);
    padding-top: 15px;
    margin-bottom: 0;

    button {
      margin-right: 10px;
    }

    a.g-button {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      text-decoration: none;
    }
  }
}
</style>
